import { diasAnio,cardinalidad,tipoanio, yearToAp, yearToIndexTypeYearInAp,sumaAniosInternosdelAp,sumardias, frecPos, frecNeg, frecPosAparato, frecNegAparato, frecAnulacion,diaSemana, diaCalendaria} from "../functions/calcs"
