 const index = yearToIndexTypeYearInAp(dateYear)


/* Aqui consigo el array con los valores del aparato,dias,diasalinicio,diasTotales */
 const datosAparato = array.find((item) => { return item.aparato === aparato})
 

/* Aqui extraigo un objeto que tiene el array que corresponde a los cuatro años del aparato */
 const diasanios = diasAnio(datosAparato.dias)
 

 const arraysolodiasanios = diasanios.array

/* Aqui escojo al año y obtengo su cantidad de dias */
 const diasanio = diasanios.array[index]


