/* Recibe los dias solares al inicio del aparato, el index , y el arrayap y devuelve los dias solares al inicio del año en cuestion  */

 const frecPosAp = frecPosAparato(frecPosAnual,index,arraysolodiasanios)

 const constante = diasolarInicioAp + frecPosAp - frecPosAnual - 734793

 const frecNegAp = frecNegAparato(frecNegAnual,index,arraysolodiasanios)

