import React from "react"
import { daysYear } from "../functions/calcs"




const CalLGC = (props) => {

const {ds,anio,fpos,cte} = props
const anios = [anio]
const valores = [fpos]
const constantes = [cte]

let acumuladorValores = fpos
let acumuladorConstantes = cte


for(let i = anio -1; i> 2012 ; i--){

    let n = daysYear(i)
    anios.push(i)
    valores.push(acumuladorValores + n)
    constantes.push(acumuladorConstantes - n)
    acumuladorValores += n
    acumuladorConstantes -= n

}

const valor2012 = ds - 735888


anios.push(2012)
valores.push(valor2012)
constantes.push(1096)

const arr = []

for( let i = 0; i < anios.length ; i++ ){
    arr.push(
        { "anio": anios[i], "valor": valores[i], "constante": constantes[i]}
    )
}

let total = 0

for(let i = 0; i< valores.length ; i++){
    total += valores[i]
}
if(ds === 735887) return <h3>Falta 1 día para el inicio del calendario LGC</h3>
if(ds< 735888) return <h3>Faltan {735888 - ds} días para el inicio del calendario LGC</h3> 


if(ds > 735888 && ds <= 734870) {
     arr.shift()
 }
    
    return ( 
        <>
       
        <h1>cal lgc</h1>
        {
            arr.map( renglon =>
                <h3 key={renglon.anio}>{renglon.anio}: {renglon.valor} ({renglon.constante})</h3>

            )
        }
        <h3>Total: {total}</h3>
        </>
        
     );
}
 
export default CalLGC;

