import { diasMeses } from "./calcs"


export function crearAnio(tipoanio,anio, diasAnio, diaSolInicial, diaSolFinal){

    let diasmes = diasMeses(diasAnio)
    
    
    /* const mes = ["Enero","Febrero","Marzo","Abril","Mayo","Junio","Julio","Agosto","Septiembre","Octubre","Noviembre","Diciembre"] */
    
    let diasiniciales = diaSolInicial
    const esteanio = []
    
   
    let acumulador = 1
     for(let i= 0; i< 12; i++ ){
         
       
            
         esteanio.push({tipoanio: tipoanio, anio: anio  ,mes: acumulador, diasMes: diasmes[i], diaSolInicial: diasiniciales, diaSolFinal:diasiniciales+ diasmes[i]-1});
         
         diasiniciales += diasmes[i];
         acumulador ++
           
       

        }
            
    
        return esteanio 
     
    }