/* Importo las matrices para utilizarlas de base en los calculos */
import { matrices } from "../arrays/calendarios"

/* Recibe el valor de un año (ej 2022) y devuelve el aparato al que corresponde ese año (ej 506)*/
export function yearToAp (dateYear){
    const aparato = Math.ceil(dateYear/4)
    return aparato
}

/* Recibe el valor de un año (ej 2022) y devuelve su index dentro del array "matrices.aniosap" */
export function yearToIndexTypeYearInAp (dateYear){
    let indexdiasanio = 0
   
    if(dateYear % 4 === 0) indexdiasanio = 3
    if(dateYear % 4 === 1) indexdiasanio = 0
    if(dateYear % 4 === 2) indexdiasanio = 1
    if(dateYear % 4 === 3) indexdiasanio = 2

    return indexdiasanio
}

/* Recibe los dias del aparato y devuelve el array de los años que contiene (ej 1461 -> [365,365,365,366]) */
export function diasAnio(dias) {

    const arrayAp = matrices.aniosap.find(item =>{
        return item.dias === dias
    })
    return arrayAp
}

export function daysYear(anio) {
  if (anio === 1582) return 355;
  if (anio < 1582 && anio % 4 === 0) return 366;
  if (anio < 1582 && anio % 4 !== 0) return 365;
  if ((anio > 1582 && anio % 4 === 0 && anio % 100 !== 0) || anio % 400 === 0)
    return 366;
  else return 365;
}

/* Recibe los dias del año y devuelve el array de los meses que contiene (ej 365 -> [31,28,31,30,31,30,31,31,30,31,30,31]) */
export function diasanioToArrayMeses(diasAnio){

    const arrayAnio = matrices.diasmeses.find(item =>{
        return item.dias === diasAnio
    })

    return arrayAnio
  }

  /* Recibe los dias solares al inicio del aparato, el indice del array del aparato y el array del aparato, devuelve la suma de dsolares al inici del año  */
export function sumaAniosInternosdelAp(dsinicioap,ind,arr){

    let array = []
  
    if(ind === 1)array.push(arr[0])
    if(ind === 2)array.push(arr[0],arr[1])
    if(ind === 3)array.push(arr[0],arr[1],arr[2])
    array.push(dsinicioap)

    const initialValue = 0;
    let res = array.reduce(
        (previousValue, currentValue) => previousValue + currentValue,
    initialValue
      )
    return res
       
    
    } 

    export function restaAniosInternosdelAp(diasarestar,ind,arr){

      let array = []
      if(ind === 0)array.push(arr[1],arr[2],arr[3])
      if(ind === 1)array.push(arr[2],arr[3])
      if(ind === 2)array.push(arr[3])
     
      array.push(diasarestar)
  
      const initialValue = 0;
      let res = array.reduce(
          (previousValue, currentValue) => previousValue + currentValue,
      initialValue
        )
      return res
         
      
      } 

export function frecPosAparato(fciapos,ind,arr){
  

  let diasrestoap = sumaAniosInternosdelAp(fciapos,ind,arr)
  return diasrestoap
  
}

export function frecNegAparato(fcianeg,ind,arr){
  
   let res = restaAniosInternosdelAp(fcianeg,ind,arr)
   return res

}

/* Recibe los dias solares al inicio del año, diasanio, el mes y el dia */
export function sumardias(suma,diasanio, mes,dia){

    let frecPositiva = frecPos(dia,mes,diasanio)
   
    let totalDiasSolares = frecPositiva + suma -1

    return totalDiasSolares

  } 
 
export function frecPos(dia,mes,diasanio){

    let newarray = []

    const arrayAp = matrices.diasmeses.find(item =>{
      return item.dias ===diasanio
    })

    if(mes===2) newarray = arrayAp.array.slice(0,-11)
    if(mes===3) newarray = arrayAp.array.slice(0,-10)
    if(mes===4) newarray = arrayAp.array.slice(0,-9)
    if(mes===5) newarray = arrayAp.array.slice(0,-8)
    if(mes===6) newarray = arrayAp.array.slice(0,-7)
    if(mes===7) newarray = arrayAp.array.slice(0,-6)
    if(mes===8) newarray = arrayAp.array.slice(0,-5)
    if(mes===9) newarray = arrayAp.array.slice(0,-4)
    if(mes===10) newarray = arrayAp.array.slice(0,-3)
    if(mes===11) newarray = arrayAp.array.slice(0,-2)
    if(mes===12) newarray = arrayAp.array.slice(0,-1) 

    newarray.push(dia)

    const initialValue = 0;

    let sumatoria = newarray.reduce(
      (previousValue, currentValue) => previousValue + currentValue,
  initialValue
    )

    return sumatoria
  
  }

export function frecNeg(dia,mes,diasanio){

    let newarray = []

    const arrayAp = matrices.diasmeses.find(item =>{
      return item.dias ===diasanio
    })
    let diasresto = arrayAp.array[mes-1] - dia
    if(mes===1) newarray = arrayAp.array.slice(1)
    if(mes===2) newarray = arrayAp.array.slice(2)
    if(mes===3) newarray = arrayAp.array.slice(3)
    if(mes===4) newarray = arrayAp.array.slice(4)
    if(mes===5) newarray = arrayAp.array.slice(5)
    if(mes===6) newarray = arrayAp.array.slice(6)
    if(mes===7) newarray = arrayAp.array.slice(7)
    if(mes===8) newarray = arrayAp.array.slice(8)
    if(mes===9) newarray = arrayAp.array.slice(9)
    if(mes===10) newarray = arrayAp.array.slice(10)
    if(mes===11) newarray = arrayAp.array.slice(11)
    if(mes===12) newarray = arrayAp.array.slice(12) 

    newarray.push(diasresto)

    const initialValue = 0;

    let sumatoria = newarray.reduce(
      (previousValue, currentValue) => previousValue + currentValue,
  initialValue
    )

    return sumatoria
  
  }
 
 export function frecAnulacion(frPos, frNeg){
   let res = frPos - frNeg
   return res
 }

 /* Recibe el valor de un dia solar y una variable que contiene el array de 875 aparatos. Devuelve el valor del aparato donde esta ese dia solar */
 export function calcAp(ds, arr) {
  
  let ap = arr.find((item) => {
    return item.diasalinicio <= ds && item.diasTotales >= ds; 
  });
  return ap.aparato;
}

/* Recibe la cantidad de dias del año y devuelve un array con la catidad de dias de cada mes (Ej 31,28,30....) */
export function diasMeses(diasAnio){

  let diasmeses = matrices.diasmeses
  let arraymeses = diasmeses.find(item =>{
    return item.dias === diasAnio
  })

 
  return arraymeses.array
}

export function diasVueltas(diasAnio){

  let diasvueltas = matrices.diasvueltas
  let arrayvueltas = diasvueltas.find(item =>{
    return item.dias === diasAnio
  })

 
  return arrayvueltas.array
}

/* Esta función es válida para determinar cualquier dia de la semana del año */
export function diaSemana(ds){
  if(ds % 7 === 0) return "viernes"
  if(ds % 7 === 1) return "sábado"
  if(ds % 7 === 2) return "domingo"
  if(ds % 7 === 3) return "lunes"
  if(ds % 7 === 4) return "martes"
  if(ds % 7 === 5) return "miércoles"
  if(ds % 7 === 6) return "jueves"
}

/* ATENCION: esta funcion es válida para determinar la posición dentro del cuadrante para dias entre 1 y 352 del año */
export function diaCuadrante(frecpos){
  if(frecpos % 4 === 0) return "contexto"
  if(frecpos % 4 === 1) return "lógica"
  if(frecpos % 4 === 2) return "inhumano"
  if(frecpos % 4 === 3) return "humano"
}

export function diaAF(frecpos){
  if(frecpos === 353 || frecpos === 366) return "centro"
  if(frecpos === 354) return "norte"
  if(frecpos === 355 ||frecpos === 356) return "noroeste"
  if(frecpos === 357) return "oeste"
  if(frecpos === 358 || frecpos === 359) return "suroeste"
  if(frecpos === 360) return "sur"
  if(frecpos === 361 || frecpos === 362) return "sureste"
  if(frecpos === 363) return "este"
  if(frecpos === 364 || frecpos === 365) return "noreste"

}

export function diaCalendaria(frecpos, diasanio){
  let valor = ""

  if(diasanio !== 355){

              if(frecpos <= 352){
                
                        valor = diaCuadrante(frecpos)
  
              }else{
              
                          valor = diaAF(frecpos)
              }

    return valor
  }
  if(diasanio === 355){

            if(frecpos <= 277){
              
              valor = diaCuadrante(frecpos)
              return valor
              }
          if(frecpos > 277 && frecpos <= 342){

              if(frecpos % 4 === 0) return "inhumano"
              if(frecpos % 4 === 1) return "humano"
              if(frecpos % 4 === 2) return "contexto"
              if(frecpos % 4 === 3) return "lógica"

              }
          if(frecpos === 343) return "centro"
          if(frecpos === 344) return "norte"
          if(frecpos === 345 || frecpos === 346 ) return "noroeste"
          if(frecpos === 347) return "oeste"
          if(frecpos === 348 || frecpos === 349 ) return "suroeste"
          if(frecpos === 350) return "sur"
          if(frecpos === 351 || frecpos === 352 ) return "sureste"
          if(frecpos === 353) return "este"
          if(frecpos === 354 || frecpos === 355 ) return "noreste"


    }
      
    }

    export function tipoanio(anio){
      
       // eslint-disable-next-line 
      switch (anio % 4) {
          case 0:
            return "Decide";
          case 1:
            return "Asume";
          case 2:
            return "Asimila";
          case 3:
            return "Desafía";
        }
      }

      export function cardinalidad(vuelta, diavuelta){
        if(vuelta < 23){

          if(diavuelta <= 4) return "SO"
          if(diavuelta >4 && diavuelta <= 8) return "NE"
          if(diavuelta >8 && diavuelta <=12) return "NO"
          if(diavuelta >12 && diavuelta <= 16) return "SE"

        }
        return "AF"

        
      }
    
    

  
 
  

 

  
    
