// eslint-disable-next-line
import React, { Component }  from 'react';
import { useState } from "react";
import { fechaActual } from "../utils/utilsDate";
import fechaActualDS from "../utils/utilsDiaSolar";
import '../stylesheets/Fecha.css'
import Form from './Form';
import CalLGC from '../elements/calLGC_renglones';
import Cuarentena from '../elements/Cuarentena';
//import { Formulario , Label, GrupoInput, Input} from '../elements/Formularios';


//import { faCheckCircle } from '@fortawesome/fontawesome-svg-core/import.macro'


/* atajo sfc + tab */
const Fecha = (props) => {

    
    const {fecha} = props

    /* Aqui consigo el valor del dia */
    const dateDate = fecha.getDate()

    /* Aqui consigo el valor del mes date.getMonth()+1*/
    const dateMonth = fecha.getMonth()+1

    /* Aqui consigo el valor del año */
    const dateYear = fecha.getFullYear()

    const [date, setDate] = useState(fechaActual(dateDate,dateMonth,dateYear))
    const [valor, setValor] = useState('')

    const [a00diasolar, a01anio, a02fposanual,a03constante] = date

   console.log(date)

    const actualizar = (ev) =>{
        ev.preventDefault(); 

        const year = Number(ev.target.year.value);
        const month = Number(ev.target.month.value);
        const dateD = Number(ev.target.date.value);
        const nuevaFecha = fechaActual(dateD,month,year)
        setDate(nuevaFecha)
        ev.target.reset()
       
    }

    const actualizarDS = (ev) => {
        ev.preventDefault()

        const ds = Number(ev.target.ds.value)
        const nuevaFecha = fechaActualDS(ds)
        setDate(nuevaFecha)
        ev.target.reset()
    }

    const restarDias = (e) => {
        e.preventDefault()
        setValor(Number(e.target.value))
     
    }
     
   

    return (
        <>
        <h3>{date[8]} {date[7]}/ {date[6] }/ {date[1]}  | Día Solar: {date[0]}</h3> 
        <button onClick={() => setDate(fechaActualDS(date[0]-1))}>Anterior</button>
        <button onClick={() => setDate(fechaActualDS(date[0]+1))}>Siguiente</button><br/>
        <button onClick={()=> setDate(fechaActualDS(date[0] - valor) )}>-</button>
        <input type='text' id='desplazar' onBlur={(e)=>restarDias(e)} autoComplete="off" placeholder="desplazar dias"></input>
        <button onClick={() => setDate(fechaActualDS(date[0]+ valor))}>+</button><br/>
        <button onClick={() => setDate(fechaActual(dateDate,dateMonth,dateYear))}>Volver a fecha Actual</button>
        
        <hr/>
        <h3>Frec. Anual: {date[2]} / -{date[9]}</h3>
        <h3>Frec. Anulación anual: {date[10]}</h3>
        <hr/>
        <h3>Año {date[5]} Aparato {date[4]}</h3>
        <h3>Frec. Aparato: {date[11]} / -{date[12]}</h3>
        <h3>Frec. Anulación aparato: {date[13]}</h3>
        <hr/>
        <h3>Fecha Calendaria: dia {date[15]} / vuelta {date[14]}</h3>
        <h3>Cardinalidad: {date[16]} Posición: {date[17]}</h3>
        <hr/>
        <h3>Día de CG: {date[0] - 734792  }</h3>
        <Cuarentena ds={date[0]} />
        <hr/>
        <h3>Cambiar la Fecha del Calendario:</h3>
        <Form onSubmit={actualizar} />
        
        <form onSubmit={actualizarDS}>
        <label htmlFor='ds'>Día solar</label>
        <input type='text' name='ds' autoComplete="off" placeholder="new solar day"></input>
        <button type='submit'>Actualizar</button>
        </form>
        <CalLGC ds={a00diasolar} anio={a01anio} fpos={a02fposanual} cte={a03constante} />
        
    


        </>
     
    );
}
 
export default Fecha;