import { useState } from "react";
import React from "react";
import {matrices} from '../arrays/calendarios'
import { daysYear } from "../functions/calcs";
import '../stylesheets/Form.css'



const Form = ({onSubmit}) => {


const [year, setYear] = useState(Number(new Date().getFullYear()))
const [month, setMonth] = useState(Number(new Date().getMonth()+1))
const [date, setDate] = useState(Number(new Date().getDate()))
const [diasanio,setDiasAnio] = useState(Number(365))
const [selectDias, setSelectDias] = useState(crearSelectDate(diasanio,month))

function crearSelectDate(da, mes){
    const arr = matrices.diasmeses.find(item =>(
        item.dias === da
    ))
    const ds = arr.array[mes-1]
    const pepa = []
    for(let i= 1; i<= ds; i++ ){
        
         
        pepa.push({index: i, mostrar:i });
       
       }
        if(da === 355 && mes === 10){
           pepa[4].mostrar = 15;
           pepa[5].mostrar = 16;
           pepa[6].mostrar = 17;
           pepa[7].mostrar = 18;
           pepa[8].mostrar = 19;
           pepa[9].mostrar = 20;
           pepa[10].mostrar = 21;
           pepa[11].mostrar = 22;
           pepa[12].mostrar = 23;
           pepa[13].mostrar = 24;
           pepa[14].mostrar = 25;
           pepa[15].mostrar = 26;
           pepa[16].mostrar = 27;
           pepa[17].mostrar = 28;
           pepa[18].mostrar = 29;
           pepa[19].mostrar = 30;
           pepa[20].mostrar = 31
       }  
       return pepa
          

}



const handleOnChangeMonth = (e) => {
    const resp = Number(e.target.value)
    setMonth(resp)
    const nuevoSelect = crearSelectDate(diasanio, resp)
    setSelectDias(nuevoSelect)
    
}




const handleOnBlurYear = (e) => {
    const res = Number(e.target.value)
    /* falta evaluar año entre valor 1 y 3500 */
    setYear(res)
    const diasAnio = daysYear(res)
    setDiasAnio(diasAnio)

    const nuevoSelect = crearSelectDate(diasAnio, month)

    setSelectDias(nuevoSelect)

   
   
}
const handleOnChangeDate = (e) => {
    const res = Number(e.target.value)
    setDate(res)
}

 const meses = matrices.meses
 


    return ( 

        <form onSubmit={onSubmit}>
            <div>
            <p>{date}  {month}  {year}</p>
            <label htmlFor='year'>Año</label>
            <input type='text' name='year' onBlur={(e) => handleOnBlurYear(e)} autoComplete="off" placeholder="new year"></input>
           
            <label htmlFor='month'>Mes</label>
            <select name="month" onChange={(e) => handleOnChangeMonth(e)}  >
                <option>Mes:</option>
                {
                    meses.map((item) =>(
                       
                       <option key={item.numero} value={item.numero} >{item.nombre}</option>
                        )
                    )
                }
              
 
            </select>
        
            <label htmlFor='date'>Día</label>
            <select name="date" onChange={(e) => handleOnChangeDate(e)}>

               
               
            <option >Dia:</option>
          
            {
                    selectDias.map((item) =>(
                       
                       <option key={item.index} value={item.index} >{item.mostrar}</option>
                        )
                    )
                }
           

            </select>
           
            <button type='submit'>Actualizar</button>
            </div>
            </form>

     );
}

 
export default Form;

