export function crearVuelta(vuelta,diasvuelta,anio,diasolarInicial){

    let acumuladordia = 1
    let acumuladordiasolar = diasolarInicial 
    
    let estemes = [
        {
            dia: 0,
            mes:0,
            anio: 0,
            diasolar: 0
        }
    ]

    for(let i=1; i<=diasvuelta;i++){
        estemes.push({
            dia:acumuladordia, vuelta:vuelta, anio: anio, diasolar:acumuladordiasolar
        })
        acumuladordia++
        acumuladordiasolar++
    }

    return (estemes)

}