export function crearMes(mes,diasmes,anio,diasolarInicial){

    let acumuladordia = 1
    let acumuladordiasolar = diasolarInicial 
    
    let estemes = [
        {
            dia: 0,
            mes:0,
            anio: 0,
            diasolar: 0
        }
    ]

    for(let i=1; i<=diasmes;i++){
        estemes.push({
            dia:acumuladordia, mes:mes, anio: anio, diasolar:acumuladordiasolar
        })
        acumuladordia++
        acumuladordiasolar++
    }

    return (estemes)

}