//import { matrices } from "../arrays/calendarios";
import { crearArray } from "../functions/creararray";
import { calcAp , cardinalidad, diaSemana, diaCalendaria} from "../functions/calcs";
import { crearAparato } from "../functions/crearaparato";
import { crearAnio } from "../functions/crearanio";
import { crearMes } from "../functions/crearmes";
import { crearAnioCalendaria} from "../functions/crearAnioCalendaria"
import { crearVuelta } from "../functions/crearvuelta";



const fechaActualDS = (diasolar) => {

const array = crearArray()


 const diasemana = diaSemana(diasolar)
 const aparatoDS = calcAp(diasolar, array)
 const primerdiaAP = array[aparatoDS].diasalinicio 
 const ultimodiaAP = array[aparatoDS].diasTotales
 const diasap = array[aparatoDS].dias
 const arrayAp = crearAparato(aparatoDS,diasap,primerdiaAP,ultimodiaAP)
 const anioBuscado = arrayAp.filter(item =>{
    return item.diaSolInicial <= diasolar && item.diaSolFinal >= diasolar
})
 const {tipoanio, anio: anioB,diasAnio:diasAB,diaSolInicial:primerDS,diaSolFinal:ultimoDS} = anioBuscado[0]

 //const arraydemeses = diasMeses(diasAB)
 const arrayAnio = crearAnio(tipoanio,anioB,diasAB,primerDS,ultimoDS)
 //const arrayAnioCalendaria = crearAnioCalendaria(tipoanio,anioB,diasAB,primerDS,ultimoDS)

 const constante = ultimoDS - 734793
 console.log(constante)

 const mesBuscado = arrayAnio.find(item =>{
    return item.diaSolInicial <= diasolar && item.diaSolFinal >= diasolar
})

 const {mes:mesB, diasMes: diasmesB, diaSolInicial:primerDSmes} = mesBuscado

 const arrayMes = crearMes(mesB,diasmesB,anioB,primerDSmes)
 const diaBuscado = arrayMes.find(item =>{
    return item.diasolar === diasolar
})

  const {dia, mes, anio, diasolar:ds} = diaBuscado 
 
 const arrayVuelta = crearAnioCalendaria(tipoanio,anio,diasAB,primerDS)

 const vueltaBuscadaDS = arrayVuelta.find(item => {
    return item.diaSolInicial <= ds && item.diaSolFinal >= ds
})

 const {diaSolInicial:primerdiaVuelta,vuelta:vueltaDS,diasvuelta}= vueltaBuscadaDS

 const vuelta = crearVuelta(vueltaDS,diasvuelta,anio,primerdiaVuelta)

 const diavuelta = vuelta.find(item => {
    return item.diasolar === ds
})

 const diavueltaDS = diavuelta.dia

 const cardinalidadDS = cardinalidad(vueltaDS,diavueltaDS)

 const frecPosApDS = diasolar - primerdiaAP +1
 const frecNegApDS = ultimodiaAP - diasolar
 const frecAnulApDS = frecPosApDS - frecNegApDS

 const frecPosAnualDS = diasolar - primerDS +1
 const frecNegAnualDS = ultimoDS - diasolar
 const frecAnulAnualDS = frecPosAnualDS - frecNegAnualDS
 const diacalendaria = diaCalendaria(frecPosAnualDS,diasAB )


 return [
    ds,
    anio,//dateYear
    frecPosAnualDS,
    constante,
    aparatoDS,
    tipoanio,//dateTipoanio
    mes,//dateMonth
    dia,//dateDate
    diasemana,//dateDiasemana
    frecNegAnualDS,
    frecAnulAnualDS,
    frecPosApDS,
    frecNegApDS,
    frecAnulApDS,
    vueltaDS,//vueltaDT
    diavueltaDS,
    cardinalidadDS,
    diacalendaria,
   

] 
   
   
}  
    
export default fechaActualDS



 


 
