import React from 'react';


const Cuarentena = (props) => {

    const {ds} = props

    

    if(ds < 734792) return <h3>Cuarentena: - </h3>

const cg = ds - 734792
const numCuarentena = Math.ceil(cg / 39)


const resto = cg % 39
let frase = ''
if(resto !== 0 && resto < 4){ frase = "ladrillo 1"}; 
if(resto > 3 && resto < 7){ frase = "ladrillo 2"};
if(resto > 6 && resto < 10){ frase = "ladrillo 3"};
if(resto > 9 && resto < 13){ frase = "ladrillo 4"};
if(resto > 12 && resto <16){ frase = "ladrillo 5"};
if(resto > 15 && resto < 19){ frase = "ladrillo 6"};
if(resto > 18 && resto < 22){ frase = "ladrillo 7"};
if(resto > 21 && resto < 25){ frase = "ladrillo 8"};
if(resto > 24 && resto < 28){ frase = "ladrillo 9"};
if(resto > 27 && resto < 31){ frase = "ladrillo 10"};
if(resto > 30 && resto < 34){ frase = "ladrillo 11"};
if(resto > 33 && resto < 37){ frase = "ladrillo 12"};
if(resto === 0  || resto > 36){frase = "ladrillo 13"}

let dias1 = [1,4,7,10,13,16,19,22,25,28,31,34,37]
let dias2 = [2,5,8,11,14,17,20,23,26,29,32,35,38]
let dias3 = [3,6,9,12,15,18,21,24,27,30,33,36,0]

let diaLadrillo = ''
if(dias1.includes(resto)) diaLadrillo = "día 1 del "
if(dias2.includes(resto)) diaLadrillo = "día 2 del "
if(dias3.includes(resto)) diaLadrillo = "día 3 del "


    return ( 
        <>
         <h3>Cuarentena: {numCuarentena} ({diaLadrillo}{frase}) </h3>

        </>
       

     );
}
 
export default Cuarentena;