import { diasVueltas } from "./calcs"


export function crearAnioCalendaria(tipoanio,anio, diasAnio, diaSolInicial, diaSolFinal= null){

    let diasvuelta = diasVueltas(diasAnio)
    
    
   
    
    let diasiniciales = diaSolInicial
    const esteanio = []
    
   
    let acumulador = 1
     for(let i= 0; i< 23; i++ ){
         
       
            
         esteanio.push({tipoanio: tipoanio, anio: anio  ,vuelta: acumulador, diasvuelta: diasvuelta[i], diaSolInicial: diasiniciales, diaSolFinal:diasiniciales+ diasvuelta[i]-1});
         
         diasiniciales += diasvuelta[i];
         acumulador ++
           
       

        }
            
    
        return esteanio 
     
    }