export function crearArray(){
    const ap = [{aparato: 0, dias: 0, diasalinicio: 0, diasTotales:0}]
    let expReg = /425|450|475|525|550|575|625|650|675|725|750|775|825|850|875/
    let total = 0
    let acumulador = 1
     for(let i= 1; i< 876; i++ ){
         
        if(expReg.test(i)){
            
                ap.push({aparato: i, dias: 1460, diasalinicio: acumulador, diasTotales:total + 1460});
                total += 1460;
                acumulador += 1460;
           
        }else if(i === 396){

                ap.push({aparato: i, dias: 1451, diasalinicio: acumulador,diasTotales:total + 1451});
                total += 1451;
                acumulador += 1451;
        }else{
                ap.push({aparato: i, dias: 1461, diasalinicio: acumulador,diasTotales: total + 1461})
            total += 1461;
            acumulador += 1461;
             }
            
    }
        return ap 
     
    }