import { diasAnio } from "./calcs"


export function crearAparato(apto, dias, diasalinicio, diasTotales){

    let diasanio = diasAnio(dias)
   
    const tipoanio = ["Asume","Asimila","Desafia","Decide"]
    let anio = apto * 4 -3
    let diasiniciales = diasalinicio
    const aparato = []
    
    
     for(let i= 0; i< 4; i++ ){
         
       
            
         aparato.push({tipoanio: tipoanio[i], anio: anio ,diasAnio: diasanio.array[i], diaSolInicial: diasiniciales, diaSolFinal:diasiniciales+ diasanio.array[i]-1});
         anio += 1;
         diasiniciales += diasanio.array[i];
           
       

        }
            
    
        return aparato
     
    }